import React from 'react';

class Register extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            email: '',
            password: '',
            name: ''
        }
    }


    onEmailChange = (event)=>{
        this.setState({email: event.target.value});
    };

    onPasswordChange = (event) =>{
        this.setState({password: event.target.value});
    };

    onNamedChange = (event) =>{
        this.setState({name: event.target.value});
    };

    onSubmitRegister = () =>{
        fetch(`${this.props.server}/register`, {
            method: 'post',
            headers: {'content-type': 'application/json'},
            body: JSON.stringify({
                email: this.state.email,
                password: this.state.password,
                name: this.state.name
            })
        })
            .then(res => res.json())
            .then(data => {
                if (data.id) {
                    this.props.userUpdate(data);
                    this.props.onRouteChange('home');
                }
            });
    };


    render(){
        return(
            <article className="br3 ba b--black-10 mv4 w-100 w-50-m w-25-l mw6 shadow-5 center">
                <main className="pa4 black-80">
                    <form className="measure">
                        <fieldset id="sign_up" className="ba b--transparent ph0 mh0">
                            <legend className="f4 fw6 ph0 mh0 center">Register</legend>
                            <div className="mt3">
                                <label className="db fw6 lh-copy f6" htmlFor="name">Name</label>
                                <input
                                    className="pa2 b--black-10 input-reset ba bg-transparent hover-bg-black hover-white w-100"
                                    type="text"
                                    name="name"
                                    id="name"
                                    onChange={this.onNamedChange}
                                ></input>
                            </div>
                            <div className="mt3">
                                <label className="db fw6 lh-copy f6" htmlFor="email-address">Email</label>
                                <input
                                    className="pa2 b--black-10 input-reset ba bg-transparent hover-bg-black hover-white w-100"
                                    type="email"
                                    name="email-address"
                                    id="email-address"
                                    onChange={this.onEmailChange}
                                ></input>
                            </div>
                            <div className="mv3">
                                <label className="db fw6 lh-copy f6" htmlFor="password">Password</label>
                                <input
                                    className="b pa2 b--black-10 input-reset ba bg-transparent hover-bg-black hover-white w-100"
                                    type="password"
                                    name="password"
                                    id="password"
                                    onChange={this.onPasswordChange}
                                ></input>
                            </div>
                        </fieldset>
                        <div className="">
                            <input
                                onClick={this.onSubmitRegister}
                                className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib"
                                type="button" value="Register"></input>
                        </div>
                    </form>
                </main>
            </article>
        );
    }


};

export default Register;